body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --text-color: white;
  --bar-height: 45px;
}
.Main {
  padding-top: 30px;
  height:calc(100vh - var(--bar-height) );
  display: flex;
  grid-gap: 0;
  gap: 0;
  flex-direction: column;
  justify-content: space-between;
}

h2, h1 {
  margin: 0;
}

h2 {
  padding-left: 10px;
  padding-top: 10px;
}

#greeter {
  background-color: rgb(27, 99, 162);
  color: white;
  text-align: center;
  padding: 80px;
}


.Navbar {
  display: flex;
  position:-webkit-sticky;
  position:sticky;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  height: var(--bar-height);
  background-color: rgb(32, 32, 32);
  top: 0;
}


.NavbarRight > Link, a {
  text-decoration: none;
  color: var(--text-color);
}

.NavbarLeft, .NavbarRight {
  display: inline-flex;
}

.NavbarLeft > a >  div, .NavbarRight > a > div {
  padding-left: 7px;
  padding-right: 7px;
  height: inherit;
  color: white;
  line-height: var(--bar-height);
  text-align: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.NavbarLeft > a > div:hover, .NavbarRight > a >  div:hover {
  transition-duration: 0.1s;
  background-color: rgb(84, 84, 84);
}

.NavbarLeft > a > div:active, .NavbarRight > a >  div:active {
  transition-duration: 0.1s;
  background-color: rgb(44, 44, 44);
}

#LoginButton {
  text-decoration: none;
  height: calc(var(--bar-height) - 7px);
  line-height: calc(var(--bar-height) - 7px);
  border-radius: 7%;
  background-color: grey;
  margin-top:   3.5px;
  margin-right: 7px;
  padding-left: 10px;
  padding-right:10px;
}

#LoginButton:hover {
  background-color: rgb(84, 84, 84);
}

#LoginButton:active {
  background-color: rgb(44, 44, 44);
}
.Footer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 0;
  height: calc(var(--bar-height)* 1.5);
  background-color: rgb(32, 32, 32);
  color: rgb(167, 167, 167);
  font-size: 12px;
}

.Footer > p {
  line-height: 0;
  font-style: italic;
}
.CardWrapper {
  width: 100%;
  height: 300px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.CardBorder {
  border-radius: 15px;
  height: 250px;
  width: 75%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.CardContent {

  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  overflow: hidden;
}

.PostCardDate {
  padding-top: 5px;
  padding-bottom: 10px;
  padding-left: 10px;
  font-style: italic;
  font-size: 14px; 
}

.PostCardDescr {
  background-color: grey;
  flex: 1 1;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
}

.PostCardHeader{
  background-color: rgb(27, 99, 162);
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  flex: 0.5 1;
}

.PostCardHeader  {
  padding-left: 10px;
}
.RootLayout {
  height:100vh;
  overflow-y: auto;
  color: black;
}

.ScrollableWrapper {
  display: flex;
  flex-direction: column;
  grid-gap: 0px;
  gap: 0px;
  width: 100%;
  height: calc(100% - var(--bar-height));
}

.LoginWrapper {
  background-color: rgb(44, 46, 46);
  flex: 2 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.LoginBox {
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
  padding: 25px;
  border-radius: 30px;
  width: 30%;
  height:45%;
  background-color: grey;
}

.LoginBox > label {
  font-weight: 900;
  font-size: 60px;
  font-style: italic;
}

.LoginBox > button {
  width: 15%;
}

.LoginFields {
  flex: 2 1;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  grid-gap: 30px;
  gap: 30px;
}

.LoginRow > label {
  font-weight: 900;
  font-size: 19px;
  font-style: italic;
}

.LoginRow > input[type=text] {
  transition-duration: 0.1s;
  box-sizing: border-box;
  width: 100%;
  border-top: none;
  border-left: none;
  border-right: none;
  background: inherit;
  border-bottom: 2px solid black;
}

.LoginRow > input[type=text]:focus {
  transition-duration: 0.1s;
  color: black;
  outline: none;
  border-top: none;
  border-left: none;
  border-right: none;
  background: inherit;
  border-bottom: 4px solid red;
}


.LoginActions {
  display: flex;
  grid-gap:10px;
  gap:10px;
}

.LoginActions > a, .LoginActions > input {
  background-color: rgb(207, 18, 18);
  padding: 5px;
  border: none;
  color: black;
}

.LoginActions > a:hover, .LoginActions > input:hover  {
  transition-duration: 0.1s;
  background-color: rgb(232, 73, 5);
}

.LoginActions > a:active, .LoginActions > input:active {
  transition-duration: 0.1s;
  background-color: rgb(106, 33, 1);
}



#Graph {
  margin: 0 auto;
}


